// hooks
import { useState } from 'react';
// icons
import arrowIcon from '../../assets/arrow-down.svg';
// style
import './TradeSelector.scss';

const TradeSelector = ({ action, setAction }) => {
  const [isVisible, setIsVisible] = useState(false);

  return (
    <div className='tradeSelector'>
      <div className='label bold-14'></div>
      <div className='selector'>
        <input
          type='text'
          className='default medium-14'
          placeholder=''
          value={action}
          onClick={() => {
            setIsVisible(!isVisible);
          }}
          onChange={(e) => setAction(e.target.value)}
        />
        {isVisible && (
          <div class='options medium-14'>
            <ul>
              <li
                onClick={() => {
                  setIsVisible(!isVisible);
                  setAction('');
                }}
              >
                
              </li>
              <li
                onClick={() => {
                  setIsVisible(!isVisible);
                  setAction('');
                }}
              >
                
              </li>
            </ul>
          </div>
        )}
        <img
          src={arrowIcon}
          alt='arrow-icon'
          onClick={() => {
            setIsVisible(!isVisible);
          }}
        />
      </div>
    </div>
  );
};

export default TradeSelector;
